}
.rr-rotate-handle {
  display: none;
  position: absolute;
  text-align: center;
  left: 50%;
  top: -30px;
  border: 1px solid #808080;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: crosshair;
  z-index: 1;
}
.rr-rotate-handle::after {
